<template>
    <nav id="inner-nav" class="inner-nav " :class="isShowNav?'show-inner-nav':''" :style="{top:scrollTop+'px'}">
        <ul class="inside-wrap">
            <li class="iconfont icon-menu menu-btn" @click="isShowMenu = !isShowMenu">
                <ul class="menu-content" :class="isShowMenu?'show-menu':''">
                    <li v-for="item in linkList" @click="clickLink(item.value)" :key="item.value">{{item.label}}</li>
                    <!--<li><router-link to="/showTai#about">ABOUT</router-link></li>-->
                    <!--<li><router-link to="/showTai#resume">RESUME</router-link></li>-->
                    <!--<li>PROTFOLIO</li>-->
                    <!--<li>CONTACT</li>-->
                </ul>
            </li>
            <li class="iconfont icon-close close-btn" @click="toBack"></li>
        </ul>
    </nav>
</template>

<script>
export default {
  name: "navPage",
  data() {
    return {
      linkList: [
        { label: "ABOUT", value: "about" },
        { label: "RESUME", value: "resume" },
        { label: "PROJECTS", value: "projects" },
        { label: "CONTACT", value: "contact" }
      ],
      isShowNav: false,
      isShowMenu: false,
      scrollTop: 0
    };
  },
  mounted() {
    // console.log(document.getElementsByTagName("body").scrollTop )
    window.addEventListener("scroll", this.handleScroll);
    setTimeout(() => {
      this.isShowNav = true;
    }, 0);
  },
  methods: {
    toBack() {
      this.$router.push("/");
    },
    clickLink(link) {
      // this.$router.push("/showTai#" + link);
      this.$emit("link", link);
    },
    handleScroll() {
      this.scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // console.log(this.scrollTop);
    }
  }
};
</script>

