<template>

   <div class="showTai">
       <about-page ref="about" id="about" ></about-page>
       <resume-page ref="resume" id="resume" ></resume-page>
        <project-page ref="projects" id="projects" ></project-page>
       <contact-page ref="contact" id="contact" ></contact-page>
           <nav-page @link="link"></nav-page>

   </div>

</template>

<script>
import aboutPage from "../components/aboutPage";
import resumePage from "../components/resumePage";
import projectPage from "../components/projectPage";
import contactPage from "../components/contactPage";
import navPage from "../components/navPage";
let total, distance, step;
export default {
  components: { aboutPage, resumePage, projectPage, contactPage, navPage },
  name: "showTai",
  data() {
    return {};
  },
  methods: {
    link(msg) {
      console.log(msg);
      total = document.getElementById(msg).offsetTop - 30;
      console.log(total);
      distance = document.documentElement.scrollTop || document.body.scrollTop;
      step = total / 100;
      if (total > distance) {
        this.smoothDown();
      } else {
        let newTotal = distance - total;
        step = newTotal / 100;
        this.smoothUp();
      }
    },
    smoothDown() {
      if (distance < total) {
        distance += step;
        document.body.scrollTop = distance;
        document.documentElement.scrollTop = distance;
        setTimeout(this.smoothDown, 10);
      } else {
        document.body.scrollTop = total;
        document.documentElement.scrollTop = total;
      }
    },
    smoothUp() {
      if (distance > total) {
        distance -= step;
        document.body.scrollTop = distance;
        document.documentElement.scrollTop = distance;
        setTimeout(this.smoothUp, 10);
      } else {
        document.body.scrollTop = total;
        document.documentElement.scrollTop = total;
      }
    }
  }
};
</script>
