    <div class="resume-page inner-pages show-inner-pages">
        <div class="resume-parallax">
            <h2 class="title">Education / Experience</h2>
            <p>
                Here is the timeline of my entire career. You can download my resume in pdf format also.
            </p>
            <div class="resume-btn">
                <el-button>DOWNLOAD CV</el-button>
            </div>
        </div>
        <div class="container">
            <div class="timeline row">
                <div class="resume-carousel" >
                    <div class="resume-wrapper-outer">
                        <div class="resume-wrapper" :style="{transform:'translate3d('+wrapperPosition+'px, 0px, 0px)',width:wrapperWidth+'px'}">
                            <div class="resume-wrapper-item" v-for="(item,index) in timelines" :key="index">
                                <div class="item">
                                    <div class="top">
                                        <span class="time">{{item.time}}</span>
                                        <span class="title">{{item.projectName}}</span>
                                        <span class="rhombus iconfont icon-tasklist"></span>
                                    </div>
                                    <div class="bottom">
                                        <p class="info">
                                            {{item.remark}}
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div class="timelineMask" ref="resumeCarousel"></div>
                        </div>

                    </div>
                </div>

            </div>

        </div>
        <div class="skills container">
            <h3 class="title skills-title">Skills</h3>
            <div class="row drawcanvas">
                <el-row>
                    <el-col :span="12">
                        <el-col :span="8">
                            <ul class="labels">
                                <li>HTML(85%)</li>
                                <li>CSS(85%)</li>
                                <li>JavaScript(80%)</li>
                                <li>Node(90%)</li>
                            </ul>
                        </el-col>
                        <el-col :span="16">
                            <canvas width="300" height="300"  ref="canvasLeft"></canvas>
                        </el-col>
                    </el-col>
                    <el-col :span="12">
                        <canvas height="300" width="500" ref="canvasRight"></canvas>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

