    // );
  },
  methods: {
    canvasInit() {
      this.canvasObj.left = this.$refs.canvasLeft;
      this.canvasObj.left_ctx = this.canvasObj.left.getContext("2d");
      this.canvasObj.right = this.$refs.canvasRight;
      this.canvasObj.right_ctx = this.canvasObj.right.getContext("2d");
      this.drawCanvas();
      this.canvasObj.left.addEventListener("click", this.leftEvent, false);
    },

    leftEvent(e) {
      // e.stopPropagation()
      // console.log(this.getEventPosition(e));
      // console.log(event);
      let x =
        event.clientX - this.canvasObj.left.getBoundingClientRect().left + 10;
      let y =
        event.clientY - this.canvasObj.left.getBoundingClientRect().top + 10;
      console.log(x, y);
      console.log(this.canvasObj.left_ctx.isPointInPath(x, y));

      this.drawCanvas({ x: x, y: y });
    },
    getEventPosition(e) {
      let x, y;
      if (e.layerX || e.layerX == 0) {
        x = e.layerX;
        y = e.layerY;
      } else if (e.offsetX || e.offsetX == 0) {
        x = e.offsetX;
        y = e.offsetY;
      }
      return { x: x, y: y };
    },
    drawCanvas(p) {
      this.canvasObj.left_ctx.clearRect(0, 0, 300, 300);
      this.skills.forEach((v, i) => {
        this.canvasObj.left_ctx.beginPath();
        this.canvasObj.left_ctx.arc(
          150,
          150,
          35 * v.level,
          v.start,
          (2 * Math.PI * v.value) / 100 + v.start,
          false
        );
        this.canvasObj.left_ctx.strokeStyle = v.color;
        this.canvasObj.left_ctx.lineWidth = v.lineWidth || 20;
        this.canvasObj.left_ctx.stroke();
        // if (p && this.canvasObj.left_ctx.isPointInPath(p.x, p.y)) {
        //
        // }
        this.canvasObj.right_ctx.beginPath();

        this.canvasObj.right_ctx.fillStyle = v.color;
        this.canvasObj.right_ctx.fillRect(
          30,
          (i + 1) * 50,
          (v.value * 500) / 100,
          30
        );
        this.canvasObj.right_ctx.font = "20px 宋体";
        this.canvasObj.right_ctx.fillStyle = "#fff";
        this.canvasObj.right_ctx.fillText(v.name, 50, (i + 1) * 50 + 20);
        this.canvasObj.right_ctx.fillText(
          v.value + "%",
          (v.value * 500) / 100 - 20,
          (i + 1) * 50 + 20
        );
        this.canvasObj.right_ctx.closePath();
      });
    },
    mousedownEvent(e) {
      // clearInterval(this.timer);
      this.mouseType = true;
      if (e.button != 2) {
        console.log(e);

        this.xDown = e.x;
      }
      // e.preventDefault();
    },
    mouseupEvent(e) {
      this.mouseType = false;
      console.log(e);
    },
    mousemoveEvent(e) {
      if (!this.mouseType) {
        return;
      }
      console.log(e);
      // let x = e.x;
    },
    wrapperInit() {
      this.wrapperWidth = (this.timelines.length + 1) * 335;
      // let i = 1;
      this.timer = setInterval(() => {
        this.wrapperIndex++;
        if (this.wrapperIndex < this.timelines.length - 2) {
          this.wrapperPosition = -this.wrapperIndex * 335;
        } else {
          this.wrapperIndex = 1;
          this.wrapperPosition = 0;
        }

        // console.log(this.wrapperPosition);
      }, 2000);
    }
  }
